#scroll-shadow {
  display: block;
  margin: auto;
  width: 140px;
  height: 80px;
  background-repeat: no-repeat;
  background-image: url("./images/scroll-icon.svg");
  background-size: 140px auto;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: grow-shrink;
}

@keyframes grow-shrink {
  from {
    transform: scale(0.7);
  }
  50% {
    transform: scale(1);
  }

  to {
    transform: scale(0.7);
  }
}

.animation, .animation_layer {
	height: 1000px;
  overflow: hidden;
}

.animation, .animation_small_layer {
	height: 1000px;
  overflow: hidden;
}

.animation {
  display: inline-block;
	position: relative;
	z-index: 10;
  height: min-content;
  overflow: hidden;
  margin: 0;
}

.animation_layer {
  background-position: 50% 0%;
  margin-top: 10%;
  background-repeat: no-repeat;
	width: 100%;
  height: 150%;
	position: absolute;
}
.animation_layer.parallax {
	position: fixed;
}

.animation_small_layer {
	background-position: 70% 50%;
	background-size: auto 200px;
	background-repeat: no-repeat;
	width: 100%;
	position: absolute;
  overflow: hidden;
}
.animation_small_layer.parallax {
	position: fixed;
  overflow: hidden;
}

