.card {
  /* Sizing */
  width: 100%;
  height: auto;
  margin: 0;
  position: relative;
  transform-style: preserve-3d;
}

.card-front,
.card-back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  padding: 30px;
  padding-top: 0px;
  border-radius: 20px;
  background-color: #444348;
  box-shadow: 0 5px 0px 6px #252427;
}

.card-front {
  transform: rotateY(0deg)
}

.card-back {
  
  transform: rotateY(180deg);
}

#positionkeeper {
  position: relative;
  visibility: hidden;
  padding: 10px;
  pointer-events: none;
}

.block {
  pointer-events: none;
  opacity: 1;
  display: block;
  width: 100%;
  height: 100%;
  transition: .5s ease;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 20px;
  background-color: #252427;
  opacity: 0;
}

.block2 {
  opacity: 1;
  display: block;
  width: 100%;
  height: 100%;
  transition: .5s ease;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 20px;
  background-color: #252427;
  opacity: 0;
}

.middle {
  pointer-events: none;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}


.card-front:hover .block {
  opacity: 0.8;
}

.card-front:hover .middle {
  opacity: 1;
}
#hover-check:hover .block {
  opacity: 0.8;
  width: 250%;
}

#hover-check:hover .middle {
  opacity: 1;
  right: 0%;
  transform: translate(-200%, -50%);
  -ms-transform: translate(-50%, -50%)
}

#hover-check-projects:hover .block2 {
  opacity: 0.8;
}

#hover-check-projects:hover .middle {
  opacity: 1;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}


#hover-check {
  position: absolute;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
}

#hover-check-projects {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

#invis {
  visibility: hidden;
}

#hovering {
  position: absolute;
  top: 0;
}

#contact-content:hover {
  color: #ffffff;
  box-shadow: 0 1px 0px 2px #252427;
  border-radius: 10px;
}

#contact-content {
  display: inline-block;
  line-height: 35px;
  margin-left: 20px;
  margin-top: 2px;
  color: #d1d1d1;
  font-size: 26px;
  padding-left: 10px;
  padding-right: 10px;
}

#resume:hover {
  color: #ffffff;
  box-shadow: 0 1px 0px 2px #252427;
  border-radius: 10px;
}

#resume {
  display: inline-block;
  line-height: 35px;
  margin-left: 20px;
  margin-top: 2px;
  color: #d1d1d1;
  font-size: 26px;
  padding-left: 25px;
  padding-right: 25px;
  box-shadow: 0 1px 0px 2px #d6d6d6;
  border-radius: 10px;
}

#instructions {
  position: absolute;
  bottom: 40px;
  right: 20px;
}

#instruction-content {
  display: inline;
  line-height: 35px;
  color: #dcdcdc;
  font-weight: 500;
  font-size: 35px;
}

#turnover {
  margin-left: 20px;
  margin-right: 20px;
  display: inline;
  width: 80px;
  color: #dcdcdc;
}